import Vue from 'vue'
import Vuex from 'vuex'

import actions from './actions'

Vue.use(Vuex)
const state = {
    userData:'',
    appParams:'',   // app参数
    indexUrl:'', // 入口链接
    Index:1,
    isAdmin:0
}

if(localStorage.getItem('token') != null && localStorage.getItem('token') != 'undefined'){
    state.userData = JSON.parse(localStorage.getItem('token'))
}
const  getters ={}
const mutations = {
    //用户信息
    modifyuserData(state, param) {
        state.userData = param
        localStorage.setItem('token',JSON.stringify(param))
    },
    // app需要的参数
    setAppParams(state, param){
        state.appParams = param;
     
    },
    // 入口链接
    setIndexUrl(state,param){
     
        state.indexUrl = param;
    },
    //状态
    oneIndex(state, param) {
        state.Index = param;
        console.log(param)
    },
    //点击发布后  存储的是否是管理员
    isAdmin(state,param) {
        state.isAdmin = param;
    }
}

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
})